<template>
  <div id="water" class="slide"  draggable="true"></div>
  <div class="dustbin"></div>
</template>
<script>
  export default {
    ready () {
      this.log('slide::ready--> init')
      this.init()
      this.bindEvents()
    },
    data () {
      return {
        debug: true, // 
        ani: [], // dom
        slice: {
          v: 5,
          h: 5
        }, // 
        timer: {
          start: 0 // 
        },
        time: {
          loop: 10000, // 
          imgflip: 30 * 18, // (18)
          imgTonext: 150 // 
        },
        bgList: ['bg1', 'bg2', 'bg3', 'bg4', 'bg5'], // 
        oldBg: 0,
        nowBg: 0
      }
    },
    methods: {
      log (a,b,c,d) {
        if (this.debug === true) {
          // console.log.apply(this, arguments)
          console.log(a,b,c,d)
        }
      },
      init () {
        var div = document.querySelector('.slide')
        div.innerHTML = ''
        var that = this,
        divWidth = div.clientWidth,
        divHeight = div.clientHeight
        that.ani = [] // dom
        var width = divWidth / that.slice.v, // div// 
        height = divHeight / that.slice.h // div
        var allDivNum = that.slice.v * that.slice.h
        that.log('div' + allDivNum)
        for (let i = 0; i < allDivNum; i++) {
          let newd = document.createElement('div')
          newd.setAttribute('class', 'fan')
          let style = ''
          style += 'width:' + width + 'px;'
          style += 'height:' + height + 'px;'
          style += 'background-position:-' + (i%that.slice.h)*width + 'px -'+ (parseInt(i/that.slice.v))*height +'px;'
          newd.setAttribute('style', style)
          newd.innerHTML = '&nbsp;'
          that.ani.push(newd)
          div.appendChild(newd)
        }
        this.start()
      },
      start () {
        var that = this
        that.ani.map(function(item, key){
          let timedelay = that.getTimeDelay(key)
          setTimeout(function () {
            // console.group('slide::start')
            that.flip(item)
            // console.groupEnd('slide::end')
          }, timedelay)
        })
        clearTimeout(that.timer.start)
        that.timer.start = setTimeout(function() {
          that.oldBg = that.nowBg
          that.nowBg = (that.nowBg + 1) % that.bgList.length
          // console.info('slide:: start' + that.nowBg)
          that.start()
        }, that.time.loop)
      },
      // idiv
      // 
      getTimeDelay (i) {
        var t = this.slice.h
        return (parseInt(i/t) + i%t + 1) * this.time.imgTonext
      },
      stop: function () {
        clearTimeout(that.timer.start)
      },
      // 18
      flip: function (dom, rotate) {
        var rotate = rotate ? rotate : 0,
        that = this,
        t = this.time.imgflip
        if (rotate <= 180) {
          var style = dom.getAttribute('style')
          style = style.replace(/transform:rotateY\(\d{1,3}deg\);/, '')
          style += 'transform:rotateY('+ (rotate > 90 ? rotate + 180 : rotate)+'deg);'
          if (rotate >= 90) {
            that.removeClass(dom, this.bgList[this.oldBg])
            that.addClass(dom, this.bgList[this.nowBg])
          }
          dom.setAttribute('style', style)
          rotate += 10
          setTimeout (function () {
            that.flip(dom, rotate)
          }, t/18)
        }
      },
      addClass (dom, klass) {
        let s = dom.getAttribute('class')
        if (s.indexOf(klass) === -1) {
          s += (' ' + klass)
          s = s.replace(/\s+/, ' ') // 
          dom.setAttribute('class', s)
        }
      },
      removeClass (dom, klass) {
        let s = dom.getAttribute('class')
        if (s.indexOf(klass) !== -1) {
          s = s.replace(klass, '')
          s = s.replace(/\s+/, ' ') // 
          dom.setAttribute('class', s)
        }
      },
      bindEvents () {
        var dragger = document.querySelector('.slide')
        var dust = document.querySelector('.dustbin')
        var body = document.querySelector('body')
        // 
        dragger.ondragstart = function (ev, arg2) {
          console.log('drag::dragger::start!', ev, arg2)

          // 
          ev.dataTransfer.setData("dragId",ev.target.id);
        }
        // 
        dragger.ondragend = function (ev, arg2) {
          console.log('drag::dragger::end!', ev, arg2)
          console.log('drag::dragger::end::', ev.toElement)
        }


        // (350ms)
        dust.ondragover = function (ev, arg2) {
          console.log('drag::dust::')
          ev.preventDefault() // 
        }
        // 
        dust.ondragenter = function (ev, arg2) {
          console.log('drag::dust::', ev, arg2)
        }
        // 
        // ondragover event.preventDefault()
        dust.ondrop = function (ev, arg2) {
          console.log('drag::dust::', ev, arg2)
          // if (ev.toElement === dust) {
          //   console.log('drag::dust::-chrome--------------------------> dust')
          //   var data = ev.dataTransfer.getData("dragId");
          //   ev.target.appendChild(document.getElementById(data));
          // }
          if (ev.target === dust) {
            console.log('drag::body::-firefox--------------------------> body')
            var data = ev.dataTransfer.getData("dragId");
            ev.target.appendChild(document.getElementById(data));
          }
          ev.preventDefault()
        }


        body.ondragover = function (ev, arg2) {
          console.log('drag::body::')
          // console.log(ev)
          ev.preventDefault() // 
        }
        body.ondrop = function (ev, arg2) {
          console.log('drag::body::', ev, arg2)
          // if (ev.toElement === body) {
          //   console.log('drag::body::-chrome--------------------------> body')
          //   var data = ev.dataTransfer.getData("dragId");
          //   ev.target.appendChild(document.getElementById(data));
          // }
          if (ev.target === body) {
            console.log('drag::body::-firefox--------------------------> body')
            var data = ev.dataTransfer.getData("dragId");
            ev.target.appendChild(document.getElementById(data));
          }

          ev.preventDefault()
        }
      }
    }
  }
</script>
<style>
.slide{
/*  position: fixed;
  z-index: -1;*/
  width: 200px;
  height: 200px;
  left:0;
  top: 0;
  background-image: url("http://images.kaistart.com/o_1av3oc6731iot1uul1hbs1fdv1bf716.jpg?imageView2/1/w/200/h/200")
}
.slide div{
  float:left;
}
.bg1{
  background-image: url("http://images.kaistart.com/o_1avsmmdrj18du1lef17osuov10ft14.jpg?imageView2/1/w/200/h/200")
}
.bg2{
  background-image: url('http://images.kaistart.com/o_1avvq5sq5kfl4g31f1uc2q1vnt19.jpg?imageView2/1/w/200/h/200')
}
.bg3{
  background-image: url('http://images.kaistart.com/o_1b0099ulps3hnpd6lktos1b7l2q.jpg?imageView2/1/w/200/h/200')
}
.bg4{
  background-image: url('http://images.kaistart.com/o_1aus9u7ge16ua13np1bupko186e1l.jpg?imageView2/1/w/200/h/200')
}
.bg5{
  background-image: url('http://images.kaistart.com/o_1av3oc6731iot1uul1hbs1fdv1bf716.jpg?imageView2/1/w/200/h/200')
}

.dustbin{
  width: 200px;
  height: 200px;
  background-color: gray;
  color: white;
}

</style>


