import Vue from 'vue'
import VueResource from 'vue-resource'
Vue.use(VueResource)

Vue.http.interceptors.push((request, next) => {
  // modify request
  request.headers.set('Auth', 'mxf')

  // //Post request payload  form data content-type
  // request.headers.set('Content-Type', 'application/x-www-form-urlencoded')

  console.log('', request)
  // continue to next interceptor
  next((response) => {
    console.log('', response)
    // modify response
    response.headers.set('Authw', 'mxf')
    response.dataMXF = { d: 'mxf ' }
  })
})



